import { db } from "./db";

type GeoJSONPoint = {
  type: string;
  coordinates: number[];
};

export async function getZipGeoById(id: string) {
  const rec = await db.zipGeoView.findUnique({ where: { zipCode: id } });
  if (!rec) return null;
  let geom: GeoJSONPoint | null = null;
  try {
    if (rec.locationGeomJson) geom = rec.locationGeomJson as GeoJSONPoint;
  } catch (e) {
    geom = null;
  }
  return {
    zipCode: rec.zipCode,
    latitude: rec.latitude ?? null,
    longitude: rec.longitude ?? null,
    location: geom,
  };
}

export async function getUserPreferenceByUserId(userId: string) {
  const rec = await db.userPreferenceView.findUnique({ where: { userId } });
  if (!rec) return null;
  return {
    userId: rec.userId,
    zipGeoId: rec.zipGeoId ?? null,
    defaultRadiusMiles: rec.defaultRadiusMiles ?? null,
    skillsSummary: rec.skillsSummary ?? null,
    resumeVectorText: rec.resumeVectorText ?? null,
    jobAlertEnabled: rec.jobAlertEnabled ?? null,
    createdAt: rec.createdAt,
    updatedAt: rec.updatedAt,
  };
}

export async function listAiEmbeddings(namespace?: string, key?: string) {
  const where: any = {};
  if (namespace) where.namespace = namespace;
  if (key) where.key = key;
  const rows = await db.aiEmbeddingStoreView.findMany({ where });
  return rows.map((r) => ({
    id: r.id,
    namespace: r.namespace,
    key: r.key,
    text: r.text,
    embeddingText: r.embeddingText ?? null,
    createdAt: r.createdAt,
  }));
}

export default {};
